import globalData from './globalData'
import scan from './scan'

function htmlString() {
  let temp = `<form class="layui-form  scan-tip tip" action="#">
  <div class="layui-form-item">
    <label class="layui-form-label">ScanGET/SSE</label>
  </div>
  <fieldset class="layui-elem-field layui-field-title">
    <legend i18n = 'arguments'></legend>
  </fieldset>
  <div class="layui-form-item">
    <label class="layui-form-label">chip:</label>
    <div class="layui-input-inline">
      <input type="text" name="chip"  value=${globalData.saved.chip!=='' ? globalData.saved.chip:'0'} placeholder="01" lay-verify='zeroOrOne'  class="layui-input">
    </div>
     <div class="layui-form-mid layui-word-aux" i18n='optional'>()</div>
  </div>
  <fieldset class="layui-elem-field layui-field-title">
    <legend i18n='description'></legend>
  </fieldset>
  <div class="layui-form-item layui-form-text">
    <div class="descriptors scan-des">
      <p><b>URL</b>URLAPI</p>
      <p><b></b>sse,MAC(bdaddr)typebdaddrTypeadData/scanDatanamerssihttp responsehttp response</p>
      <p><b> chip</b>01queryString(?chip=0?chip=1),0010</p>
      <p><b>SSE</b>server-sent eventsseehttpdata:  sseurlhttp(http)iOS/java/nodejs/js/c#demossesse</p>
    
    </div>
  </div>
  
  <div class="layui-form-item">
    <div class="layui-input-block">
      <button class="layui-btn" lay-submit lay-filter="scan">do</button>
    </div>
  </div>
</form>`
  return temp
}


function scanTip(layer, form, $dom) {
  form.verify({
    zeroOrOne: function (value) {
      // debugger
      if (!(value === '0' || value === '1' || value === '')) {
        return '01';
      }
    }
  });

  function dos(layer, form) {

    form.on('submit(scan)', function (data) {
      // debugger
      globalData.saved.chip = data.field.chip
      $('#scanSwitch').prop('checked', true)
      scan.start({
        chip: globalData.saved.chip || 0
      }, globalData.timeOut)
      
      form.render('checkbox')
      layer.closeAll('tips');
      return false
    });
  }
  tip(layer, htmlString, $dom, dos, form)


}


import tip from './tips'
export default scanTip


